import React, {useEffect, useState} from "react";
import axios from 'axios';
import List from "../List/List";
import s from './AddList.module.scss';
import Badge from "../Badge/Badge";
import close from '../../images/close.svg';


const AddList = ({onAddList, items, colors}) => {
  // onAddList - –¥–æ–±–∞–≤–ª–µ–Ω–∏–µ –Ω–æ–≤. —Ç–∞—Å–∫–∏ –∏ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ —Å–æ—Å—Ç–æ—è–Ω–∏—è
  // colors - –º–∞—Å—Å–∏–≤ —Ü–≤–µ—Ç–æ–≤ –¥–ª—è –ø–æ–ø–∞–ø–∞
  const [visiblePopup, setVisiblePopup] = useState(false) // –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏–µ –ø–æ–ø–∞–ø–∞
  const [selectedColor, setSelectColor] = useState(3) // –≤—ã–±–æ—Ä –∞–∫—Ç–∏–≤–Ω–æ–≥–æ —Ü–≤–µ—Ç–∞ –≤ –ø–æ–ø–∞–ø–µ
  const [isLoading, setIsLoading] = useState(false)
  const [inputValue, setInputValue] = useState('') // value –∏–Ω–ø—É—Ç–∞
  const [fieldValid, setFieldValid] = useState(false) // –≤–∞–ª–∏–¥–∞—Ü–∏—è –∏–Ω–ø—É—Ç–∞
  useEffect(() => {
    if (Array.isArray(colors)) {
      setSelectColor(colors[0].id)
    }
  }, [colors])

  const onClose = () => {  // —Å–±—Ä–æ—Å–∏—Ç—å –ø–æ–ø–∞–ø –≤ –¥–µ—Ñ–æ–ª—Ç
    setInputValue('');
    setSelectColor(colors[0].id);
    setFieldValid(false)
  }

  function openPopup() {
    setVisiblePopup((visiblePopup) => !visiblePopup)
    onClose()
  }

  function addList(event) {  // –¥–æ–±–∞–≤–ª–µ–Ω–∏–µ –Ω–æ–≤–æ–≥–æ —Å–ø–∏—Å–∫–∞ –∏–∑ –ø–æ–ø–∞–ø–∞
    event.preventDefault()
    if (!inputValue) {
      return setFieldValid(true) // –ø—Ä–∏ –∫–ª–∏–∫–µ –î–û–ë–ê–í–ò–¢–¨ –∏–Ω–ø—É—Ç –ø–æ–ø–∞–ø–∞ –ø—É—Å—Ç–æ–π? –≤–∞–ª–∏–¥–∞—Ü–∏—è  true
    } else {
      setFieldValid(false)
    }
    const color = colors.filter(c => c.id === selectedColor)[0].name
    setIsLoading(true)
    // axios.post('http://localhost:3001/lists', {name: inputValue, colorId: selectedColor, color: color})
    axios.post('https://todo-165ea.firebaseio.com/todo/lists.json',
      {
        color: color,
        colorId: selectedColor,
        id: Math.floor(Math.random() * 10000) + 1,
        tasks:[],
        name: inputValue
      })
      .then((res) => {
        const data = JSON.parse(res.config.data)
        const listObj = {...data}
        onAddList(listObj)
        console.log(data)
        setVisiblePopup(false)
        onClose()
      })
      .finally(() => {
        setIsLoading(false)
      })
    // onAddList({   // –ø–µ—Ä–µ–¥–∞—á–∞ –Ω–æ–≤–æ–≥–æ –æ–±—ä–µ–∫—Ç–∞ –≤ –º–∞—Å—Å–∏–≤ —Å–ø–∏—Å–∫–∞
    //   id: Math.floor(Math.random() * 100) + 1,
    //   name: inputValue,
    //   color: color
    // });

  }

  return (

    <div className={s.addList}>
      <List  // –∫–æ–º–ø–æ–Ω–µ–Ω—Ç   "+–î–æ–±–∞–≤–∏—Ç—å —Å–ø–∏—Å–æ–∫"
        openPopup={openPopup}
        items={[
          {
            list__addButton: 'list__addButton',
            name: '–î–æ–±–∞–≤–∏—Ç—å —Å–ø–∏—Å–æ–∫',
            icon: (
              <svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg"
                   opacity="1">
                <path d="M6 1V11" stroke="#000000" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round"/>
                <path d="M1 6H11" stroke="#000000" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round"/>
              </svg>
            )
          }
        ]}
      />


      {visiblePopup &&
      (<div className={s.addPopup}>
        <img             // –∫—Ä–µ—Å—Ç–∏–∫ –∑–∞–∫—Ä—ã—Ç–∏—è –ø–æ–ø–∞–ø–∞
          onClick={() => {
            onClose();
            setVisiblePopup(false)
          }}
          className={s.addPopup__closePopup}
          src={close} alt="–∑–∞–∫—Ä—ã—Ç—å"/>
        <input
          value={inputValue}
          onChange={(e) => setInputValue(e.target.value)}
          className={s.field} type="text" placeholder="–ù–∞–∑–≤–∞–Ω–∏–µ –ø–∞–ø–∫–∏"/>
        {fieldValid ? <p className={s.field__valid}>–ü–æ–ª–µ –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –ø—É—Å—Ç—ã–º</p> : <p className={s.field__valid}></p>}
        <ul className={s.addPopup__listColor}>
          {/*—Ä–µ–Ω–¥–µ—Ä —Ü–≤–µ—Ç–æ–≤ –≤ –ø–æ–ø–∞–ø–µ*/}
          {colors ? (
            colors.map((item) => {
              const color = `badge__${item.name}`

              function handleClickPopup() {
                setSelectColor(item.id)
              }

              return (

                <li key={item.id}>
                  <Badge
                    handleClickPopup={handleClickPopup}
                    color={color}
                    stylePopup={'badge_stylePopup'}
                    stylePopup–êctive={selectedColor === item.id && 'badge_stylePopup–êctive'}
                  />
                </li>
              )
            })
          ) : ('–ó–∞–≥—Ä—É–∑–∫–∞ —Ü–≤–µ—Ç–æ–≤...')
          }
          {/*—Ä–µ–Ω–¥–µ—Ä —Ü–≤–µ—Ç–æ–≤ –≤ –ø–æ–ø–∞–ø–µ*/}
        </ul>
        <button type="submit" onClick={addList} className={s.addPopup__btn}>
          {isLoading ? '–î–æ–±–∞–≤–ª–µ–Ω–∏–µ...' : '–î–æ–±–∞–≤–∏—Ç—å'}
        </button>
      </div>)}
    </div>
  );
};

export default AddList