import React, {useState} from "react";
import axios from 'axios';
import classNames from 'classnames';
import List from "../List/List";
import s from './Tascs.module.scss';
import addTaskSvg from '../../images/addTask.svg';

const AddTaskForm = ({list, onAddTask, dataList}) => {
  const [visibleForm, setVisibleForm] = useState(false);  // –≤–∏–¥–∏–º–æ—Å—Ç—å —Ñ–æ—Ä–º—ã
  const [inputValue, setInputValue] = useState(''); // –ø–æ–ø–∞–ø –≤ —Ñ–æ—Ä–º–µ –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é –ø—É—Å—Ç–æ–π
  const [isLoading, setIsLoading] = useState(false)
  const [fieldValid, setFieldValid] = useState(false) // –≤–∞–ª–∏–¥–∞—Ü–∏—è –∏–Ω–ø—É—Ç–∞

  const toggleFormVisible = () => {
    setVisibleForm((visibleForm) => !visibleForm);
    setInputValue('')
    setFieldValid(false)
  }

  const addTask = () => {

    if (!inputValue) {
      return setFieldValid(true) // –ø—Ä–∏ –∫–ª–∏–∫–µ –î–û–ë–ê–í–ò–¢–¨ –∏–Ω–ø—É—Ç –ø–æ–ø–∞–ø–∞ –ø—É—Å—Ç–æ–π? –≤–∞–ª–∏–¥–∞—Ü–∏—è  true
    } else {
      setFieldValid(false)
    }
    setIsLoading(true)
    const obj = {
      completed: false,
      text: inputValue,
      id: Math.floor(Math.random() * 100) + 1,
      listId: list.colorId,
    }

    for (let key in dataList) {
      if (dataList[key].id == list.id) {
        axios.post(`https://todo-165ea.firebaseio.com/todo/lists/${key}/tasks.json`, obj)
          .then((res) => {
            const dataObj = JSON.parse(res.config.data)
            onAddTask(list.id, dataObj)
            toggleFormVisible()
          })
          .finally(() => {
            setIsLoading(false)
          })
      }
    }


  }

  return (
    <>
      {!visibleForm ? (
        <div onClick={toggleFormVisible} className={s.tasks__formNew}>
          <img src={addTaskSvg} alt="Add icon"/>
          <span>–î–æ–±–∞–≤–∏—Ç—å –∑–∞–¥–∞—á—É</span>
        </div>
      ) : (
        <div className={s.tasks__formBlock}>
          <input
            value={inputValue}
            onChange={e => setInputValue(e.target.value)}
            className={s.field}
            type="text"
            placeholder="–ù–∞–∑–≤–∞–Ω–∏–µ –∑–∞–¥–∞—á–∏"
          />
          {fieldValid ? <p className={s.tasks__valid}>–ü–æ–ª–µ –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –ø—É—Å—Ç—ã–º</p> : <p className={s.tasks__valid}></p>}
          {/*<p className={s.tasks__valid}>–ü–æ–ª–µ –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –ø—É—Å—Ç—ã–º</p>*/}
          <button disabled={isLoading} onClick={addTask} type="submit" className={s.tasks__addTascs}>
            {isLoading ? '–î–æ–±–∞–≤–ª–µ–Ω–∏–µ...' : '–î–æ–±–∞–≤–∏—Ç—å –∑–∞–¥–∞—á—É'}
          </button>
          <button onClick={toggleFormVisible} type="submit"
                  className={classNames(s.tasks__addTascs, s.tascs__addTascs_gray)}>
            –û—Ç–º–µ–Ω–∞
          </button>
        </div>
      )

      }


    </>
  )
}

export default AddTaskForm